body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
  overflow-x: hidden;
}

/*
The below two classes renders the loading animation for several pages 
and hence is kept in index.css itself
*/

.loader {
  border-top: 4px solid orange; 
  border-right: 4px solid #333; 
  border-bottom: 4px solid orange;
  border-left: 4px solid #333;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  -webkit-animation: spin 0.5s linear infinite;
          animation: spin 0.5s linear infinite
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.container{
  padding: 0;
  margin: 0;
  width: 100%
}

.col-lg-6, .col-md-6, .col-sm-6{
    padding-bottom: 100%;
    margin-bottom: -100%;
}

.footer-link:link{
  color: orange;
  text-decoration: none;
}

.footer-link:visited{
  color: orange;
  text-decoration: none;
}

.footer-link:hover{
  color: orange;
  text-decoration: underline;
}

.footer-link:active{
  color: orange;
  text-decoration: none;
}

.row {
    overflow: hidden;
}

.footer {
  height: 100px; 
  text-align: center; 
  padding-top: 15px; 
  margin-bottom: 25px;
  color: white; 
  font-family: 'Cabin';
}

.footer-chevron {
  font-size: 35px;
  background: transparent;
  border-color: transparent;
}

.footer-chevron:hover {
  color: orange;
  transition: 0.4s;
}

@media (max-width: 768px) {
  .col-lg-6, .col-md-6, .col-sm-6 {
    padding-bottom: 10px;
    padding-top: 7px;
    margin-bottom: 0px;
  }
}
.teamStyle {
  text-align: center;
  background: #CCC;
  color: #333;
  padding-top: 50px;
  height: 100%;
  border: 3px;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  margin: 0px;
}

.teamImg {
  width: 150px;
  height: 150px;
}

.teamStyle a{
  color: black;
  font-weight: bold;
  text-decoration: underline;
}

.teamStyle a:visited{
  color:black;
  font-weight: bold;
  text-decoration: underline;
}

.teamStyle a:hover {
  color:orange;
  font-weight: bold;
  text-decoration: underline;
}

.teamStyle a:active{
  color:black;
  font-weight: bold;
  text-decoration: none;
}
.aboutUsStyle {
  text-align: center;
  background: #CCC;
  color: #333;
  padding-top: 30px;
  height: auto;
  border: 3px;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  margin: 0px;
}

.aboutContainer {
  position: relative;
  text-align: center;
  color: black;
}

.aboutHeading {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: 'Julius Sans One';
}

.aboutImg{
  max-width: 100%;
  width: auto;
  height: auto;
  opacity: 0.5;
}

.aboutContent{
  padding: 20px 5% 20px 5%;
}

.aboutContent h1{
  font-family: 'Cabin';
}

.aboutContent p{
  font-size: 14px;
  text-align: justify;
}
.logoMobile{
	position: relative;
	top: 29%;
	left: 38%;
}

.logoSize{
	height:22%;
	width:22%;
}

@media 
only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {

.logoMobile{
position: relative;
top: 35%;
left: 35%;
}

.logoSize{
  height: 30%;
  width: 30%;
}

}
.newsStyle {
  color: #BBB;
  text-align: left;
