}

.newsStyle h1{
  color: #BBB;
  text-align: center;
  font-family: "Cabin";
}

.newsStyle a{
  color:white;
  font-weight: bold;
  text-decoration: none;
}

.newsStyle a:visited{
  color:white;
  font-weight: bold;
  text-decoration: none;
}

